import React from 'react'
import get from 'lodash/get'
import Helmet from 'react-helmet'
import { makeStyles } from '@mui/styles';
import SimpleLayout from "../components/SimpleLayout/SimpleLayout"
import { Link, graphql } from 'gatsby'

import 'typeface-playfair-display'
import 'typeface-montserrat'
import 'typeface-cormorant-garamond'

import Card from "@mui/material/Card"
import CardContent from "@mui/material/CardContent"
import CardMedia from "@mui/material/CardMedia"

const useStyles = makeStyles({
  container: {
    columnCount: 2,
    columnGap: "3%",
    columnWidth: "400px"
  },
  blogPostCard: {
    width: "100%",
    breakInside: "avoid",
    padding: '20px',
    marginBottom: '35px',
  },
  title: {
    margin: '0.5rem 0.0rem',
    font: '200 22px montserrat, sans-serif',
  },
  titleContainer: {
    display: "flex",
    alignItems: "baseline",
    justifyContent: "space-between",
  },
  excerpt: {
    font: '400 18px cormorant garamond, sans-serif',
    color: 'black',
  },
  date: {
    font: '200 12px montserrat, sans-serif',
    color: 'black',
  },
  media: {
    height: 140,
  },
})

const Blog = (props) => {
  const classes = useStyles()
  const siteTitle = get(props, '.data.site.siteMetadata.title')
  const posts = get(props, 'data.allMarkdownRemark.edges')

  return (
    <SimpleLayout>
      <Helmet title={siteTitle} />
      <div className={classes.container}>
        {posts.map(({ node }) => {
          const title = get(node, 'frontmatter.title') || node.fields.slug
          const link = `../${node.frontmatter.date}${node.fields.slug}`
          const excerpt = node.excerpt

          return (
            <Card key={title} className={classes.blogPostCard}>
              <Link to={link}>
                { node.frontmatter.image?
                  <CardMedia
                    className={classes.media}
                    image={node.frontmatter.image.childImageSharp.fluid.src}
                    title={title}
                  /> : null
                }
                <CardContent>
                  <div className={classes.titleContainer}>
                    <h2 className={classes.title}>
                      {title}
                    </h2>
                    <p className={classes.date}>
                      {node.frontmatter.date}
                    </p>
                  </div>
                  <p className={classes.excerpt}>
                    {excerpt}
                  </p>
                </CardContent>
              </Link>
            </Card>
          )
        })}
      </div>
    </SimpleLayout>
  )
}

export default Blog

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
        description
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          excerpt(pruneLength: 250)
          fields {
            slug
          }
          frontmatter {
            title
            date
            image {
              childImageSharp {
                fluid(maxWidth: 800) {
                  src
                }
              }
            }
          }
        }
      }
    }
  }
`
